@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{padding:0 1rem;display:flex;justify-content:left;align-items:center;text-transform:uppercase;border-bottom:solid 1px rgba(160,160,160,.3);background-color:#fff}.nav-item{text-decoration:none;color:inherit}.index-link{color:#deb887;margin:.3rem 0}.links ul{list-style:none;display:flex}.links ul li{margin:0 2rem}.fa-bars{display:none}@media only screen and (max-width: 786px){.fa-bars{display:block;cursor:pointer;flex:0 0 30%;font-size:25px}header{flex-wrap:wrap;justify-content:space-between}h2{flex:0 0 50%}.index-link{text-align:center}.links{height:auto;min-height:50px;flex:0 0 1;display:none;width:70%;transition:all .3s ease-in-out}.links ul.collapsed{width:100%;display:flex;flex-direction:column;justify-content:space-between;flex-wrap:wrap;overflow:hidden;max-height:0;transition:all .3s ease}.links ul.collapsed.is-expanded{overflow:hidden;padding:0 15%;max-height:500px;transition-duration:.1s;transition-timing-function:ease-in}.links ul.collapsed li{padding:5px 10px;margin:0px 0px;width:100%}.links ul.collapsed li:not(:last-child){border-bottom:dotted 1px rgba(160,160,160,.3)}}.active{color:red;transition:.2s ease;border-bottom:solid #deb887 2px}.d-block{display:block}
.footer{display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;width:90%;padding:5%}.footer_info-img{margin-top:2rem;border-radius:50%;width:200px}.footer_info h1{text-transform:uppercase}.footer_info a{color:#deb887;text-decoration:none}.footer_about{margin-top:2rem;border-top:solid 1px rgba(160,160,160,.3);text-align:left}.footer p{color:#646464}.footer .social ul{list-style:none;display:flex;padding:0;margin:.5rem 0}.footer .social ul li{padding:0 1rem;font-size:1.5rem;height:1.5rem}.footer .social ul li a{color:#aaa}.footer .social ul li a:hover{color:purple}@media(max-width: 960px){.footer{flex-direction:column-reverse}}
body{
  background: #F4F4F4;
}

.contents {
  padding-top: 3rem;
  display: flex;
}

.main {
  background-color: white;
  flex: 66% 1;
  margin: 0 1rem;
  border: solid 1px rgba(160, 160, 160, 0.3);
}

.footer-container {
  flex: 33% 1;
} 

@media only screen and (max-width: 960px){
  .contents { flex-direction: column;}
  .footer-container {flex: 1 1; order: 2; }
  .main  {flex: 1 1; order: 1 }
}
.loading-container{font-family:"Roboto",sans-serif;font-size:1rem;line-height:1.5;display:flex;align-items:center;justify-content:center;margin:0;min-height:100vh;background:#fff;overflow:hidden}.loading{position:relative;transform:scale(0.725)}.divider{position:absolute;z-index:2;top:65px;left:200px;width:50px;height:15px;background:#fff}.loading-text{position:relative;font-size:3.75rem;font-weight:300;margin:0;white-space:nowrap}.loading-text::before{position:absolute;content:"";z-index:1;top:40px;left:115px;width:6px;height:6px;background:#000;border-radius:50%;animation:dotMove 1800ms cubic-bezier(0.25, 0.25, 0.75, 0.75) infinite}.loading-text .letter{display:inline-block;position:relative;color:#000;letter-spacing:8px}.loading-text .letter:nth-child(1){transform-origin:100% 70%;transform:scale(1, 1.275)}.loading-text .letter:nth-child(1)::before{position:absolute;content:"";top:22px;left:0;width:14px;height:36px;background:#fff;transform-origin:100% 0;animation:lineStretch 1800ms cubic-bezier(0.25, 0.25, 0.75, 0.75) infinite}.loading-text .letter:nth-child(5){transform-origin:100% 70%;animation:letterStretch 1800ms cubic-bezier(0.25, 0.23, 0.73, 0.75) infinite}.loading-text .letter:nth-child(5)::before{position:absolute;content:"";top:15px;left:2px;width:9px;height:15px;background:#fff}@keyframes dotMove{0%,100%{transform:rotate(180deg) translate(-110px, -10px) rotate(-180deg)}50%{transform:rotate(0deg) translate(-111px, 10px) rotate(0deg)}}@keyframes letterStretch{0%,100%{transform:scale(1, 0.35);transform-origin:100% 75%}8%,28%{transform:scale(1, 2.125);transform-origin:100% 67%}37%{transform:scale(1, 0.875);transform-origin:100% 75%}46%{transform:scale(1, 1.03);transform-origin:100% 75%}50%,97%{transform:scale(1);transform-origin:100% 75%}}@keyframes lineStretch{0%,45%,70%,100%{transform:scaleY(0.125)}49%{transform:scaleY(0.75)}50%{transform:scaleY(0.875)}53%{transform:scaleY(0.5)}60%{transform:scaleY(0)}68%{transform:scaleY(0.18)}}@media(min-width: 48rem){#container{transform:scale(0.725rem)}}@media(min-width: 62rem){#container{transform:scale(0.85)}}
